@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  line-height: 1.625rem;
  font-family: "Open Sans", sans-serif;
  background: #16161a;
  max-width: 840px;
  margin: 0 auto;
  color: #fffffe;
  font-weight: 600;
}

ul {
  list-style: none;
}

h1 {
  font-size: 2rem;
}

p {
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.animeShow {
  transform: translateX(0px);
  opacity: 0;
  animation: animeShow 0.5s forwards;
}

@keyframes animeShow {
  to {
    transform: translateX(inital);
    opacity: 1;
    opacity: initial;
  }
}

.content {
  flex: 1 1;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh + 10rem);
}

.organicLoading {
  width: 50%;
  height: 50%;
  background: linear-gradient(135deg, #547e28, #6f9631);
  border-radius: 41% 59% 41% 59% / 43% 45% 55% 57%;
  animation: organicLoading 3s infinite;
}

@keyframes organicLoading {
  0% {
    border-radius: 41% 59% 41% 59% / 43% 45% 55% 57%;
  }
  33% {
    border-radius: 30% 70% 30% 70% / 32% 30% 70% 68%;
  }
  66% {
    border-radius: 70% 30% 70% 30% / 68% 70% 30% 32%;
  }
  100% {
    border-radius: 41% 59% 41% 59% / 43% 45% 55% 57%;
  }
}


